import { render } from '@testing-library/preact';

import { expect } from 'chai';

import { SelectMenuItem } from '../SelectMenuItem';
import { getIcon, getMenuItemByRole, getLabel } from './menuTestsUtils';

describe('Test SelectMenuItem Component', () => {
  it('Default MenuItem with just label renders correctly', async function () {
    const content = render(<SelectMenuItem value="menuItem1" label="MenuItem1" />);

    const menuItem = getMenuItemByRole(content, 'menuitemradio');
    const label = getLabel(content, 'MenuItem1');

    const disabled = menuItem.getAttribute('aria-disabled');
    const tabindex = menuItem.getAttribute('tabindex');

    expect(menuItem).not.null;
    expect(label).not.null;
    expect(menuItem.dataset['ojMenuItemKey']).not.undefined;
    expect(tabindex).to.equal('-1');
    expect(disabled).null;
  });

  it('Disabled MenuItem renders correctly', async function () {
    const content = render(<SelectMenuItem value="menuItem1" label="MenuItem1" isDisabled />);

    const menuItem = getMenuItemByRole(content, 'menuitemradio');

    expect(menuItem).not.null;
    expect(menuItem.getAttribute('aria-disabled')).equals('true');
  });

  it('MenuItem with endIcon renders correctly', async function () {
    const content = render(
      <SelectMenuItem value="menuItem1" label="MenuItem1" endIcon={<span class="endIcon"></span>} />
    );

    const menuItem = getMenuItemByRole(content, 'menuitemradio');
    const label = getLabel(content, 'MenuItem1');
    const endIcon = getIcon(menuItem, '.endIcon');

    expect(menuItem).not.null;
    expect(label).not.null;
    expect(endIcon).not.null;
  });
});
